const regex = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
import {
	appUpdUserInfoByCode,
	sendMSM
} from '../../../api/auth.js'
import {
	putUserInfo
} from '../../../setting/putUserInfo.js'
export default {
	data() {
		return {
			account: null,
			code: null, //验证码
			password: null,
			codeStatus: false, //验证码发送状态，默认为不发送，即允许发送
			num: null, //秒数
		}
	},
	methods: {
		findPassword() {
			if (!regex.test(this.account)) {
				uni.showToast({
					icon: 'none',
					title: '请填写正确的手机号'
				});
				return;
			}
			if (!this.code) {
				uni.showToast({
					icon: 'none',
					title: '请填写验证码'
				});
				return;
			}
			if (!this.password) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				});
				return;
			}
			const updData = {
				password: this.password,
				code: this.code
			}
			appUpdUserInfoByCode(updData).then(data => {
				console.log(data)
				if (data.errno == 200) {
					uni.showToast({
						title: '修改成功'
					});
					putUserInfo(data).then(data => {
						console.log(data)
						uni.navigateBack({
							delta: 1
						});
					}).catch(err => {
						console.log(err)
					})
					uni.navigateBack({
						delta: 1
					});
				}
			}).catch(err => {
				console.log(err)
			})
		},
		sendCode() {
			if (!this.codeStatus) {
				//默认给60秒
				this.num = 60;
				this.codeStatus = true;
				sendMSM({
					telephone: this.account,
					need: 'isExist'
				}).then(data => {
					if (data.errno == 200) {
						uni.showToast({
							icon: 'none',
							title: '发送成功，注意查收'
						})
					} else {
						uni.showToast({
							icon: 'none',
							title: `发送失败,${data.errmsg}`
						})
					}
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: `发送失败,${err}`
					})
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请耐心等待'
				});
			}
		}
	},
	watch: {
		num(newVal, oldVal) {
			if (newVal === 0) {
				this.codeStatus = false;
				return;
			}
			setTimeout(() => {
				this.num--;
			}, 1000)
		}
	}
}
